  connectMetamask = async () => {
    const provider = await detectEthereumProvider();
    console.log(provider);
    const web3 = new Web3(provider);

    if (provider !== window.ethereum) {
      console.error('Do you have multiple wallets installed?');
    };
    const accounts = await provider.request({ method: 'eth_requestAccounts' });
    const networkId = await provider.request({ method: 'eth_chainId' });


    if (networkId === "0x4") {
      const deployedNetwork = RinkebyDeposit.networks[networkId];
      const instanceRinkeby = new web3.eth.Contract(
        //rinkeby
        RinkebyDeposit.abi,
        deployedNetwork && '0x4F7DF15D045c250302d9fb0E5e04A28f1435cC45',
      );
      instanceRinkeby._address = '0x4F7DF15D045c250302d9fb0E5e04A28f1435cC45';
      console.log(instanceRinkeby);

      const wETHNetwork = wETH.networks[networkId];
      const instancewETH = new web3.eth.Contract(
        //rinkeby
        wETH.abi,
        wETHNetwork && '0xc778417e063141139fce010982780140aa0cd5ab',
      );
      instancewETH._address = '0xc778417e063141139fce010982780140aa0cd5ab';
      console.log(instancewETH);

      this.setState({ contract: instanceRinkeby, networkId: networkId, rinkebywETH: instancewETH });
    }
    else if (networkId === "0xc1b54a854098") {
      const deployedNetwork = ArbitrumDeposit.networks[networkId];
      const instanceArbitrum = new web3.eth.Contract(
        //arbitrum
        ArbitrumDeposit.abi,
        deployedNetwork && '0x1Fb89f92807381252ba247CCF1aA97E1204B20Bb',
      );
      instanceArbitrum._address = '0x1Fb89f92807381252ba247CCF1aA97E1204B20Bb'; //有待争议？？？
      // console.log(instanceArbitrum);
      this.setState({ contract: instanceArbitrum, networkId: networkId });
    }

    // console.log(chainId);

    await this.setState({
      web3: web3,
      accounts: accounts,
      networkId: networkId
    });

    // document.querySelector('.shuaXin').innerHTML = "Automatically refresh the page after 3 seconds!";

    document.querySelector('.Account').innerHTML = accounts[0].substring(0, 8) + "...";

    if (this.state.networkId !== "0x4") {
      document.querySelector('.Network').innerHTML = "Please switch to the Rinkeby test network!";
    }


    if (this.state.networkId === "0x4") {
      document.querySelector('.Network').innerHTML = "Rinkeby";
    } else if (this.state.networkId === "0xc1b54a854098") {
      document.querySelector('.Network').innerHTML = "Arbitrum";
    }

    // setTimeout(myRefresh, 3000); //3秒
    // function myRefresh() {
    //   window.location.reload();
    // }

    this.getUniswapPrice();
  };


