  startDeposit = async () => {
    const { accounts, contract, rinkebywETH } = this.state;

    // Stores a given value, 5 by default.
    // await contract.methods.set(5).send({ from: accounts[0] });

    let _jia = this.jia.current.value;
    let _bing1 = this.bing1.current.value;
    let _bing2 = this.bing2.current.value;
    let _bing3 = this.bing3.current.value;
    let _bing4 = this.bing4.current.value;
    let _bing5 = this.bing5.current.value;
    let _bing6 = this.bing6.current.value;
    let _bing7 = this.bing7.current.value;
    // let _time = this.time.current.value;
    let _time = this.time.current.value * 8640;
    let _yajin = this.yajin.current.value;

    let _notQuarter = 0; //false
    let _notHalf = 0; //false

    let _bing = [];
    let finalBing = [];
    let _bing2Display = document.getElementById("Bing2").style.display;

    _bing.push(_bing1, _bing2, _bing3, _bing4, _bing5, _bing6, _bing7);


    if (_bing2Display === "" || _bing2Display === "none") {
      finalBing.push(_bing1);
    }
    else {
      for (let i = 0; i < _bing.length; i++) {
        if (_bing.indexOf(_bing[i]) !== -1) {
          document.querySelector(".createDeposit").innerHTML = "Please do not enter the judge with the same wallet address";
          return;
        }
        if (_bing[i] !== "") {
          finalBing.push(_bing[i]);
        }
        else {
          _bing[i] = null;
        }
      }
    }



    let _notQuarterColor = document.getElementById("Quarter").style.backgroundColor;
    let _notHalfColor = document.getElementById("Half").style.backgroundColor;
    if (_notQuarterColor === "gold") {
      _notQuarter = 1; //true
    };

    if (_notHalfColor === "gold") {
      _notHalf = 1; //true
    };



    let ETHcolor = document.getElementById("ETH").style.backgroundColor;
    let wETHcolor = document.getElementById("wETH").style.backgroundColor;

    console.log(this.state.contract);

    document.querySelector(".createDeposit").innerHTML = "Deposit is being created...";

    if (this.state.networkId === 4 || this.state.networkId === "0x4") {

      if (ETHcolor === "gold" || ETHcolor === "") {
        console.log(ETHcolor);

        let _tokenName = "ETH";
        try {

          await contract.methods.startDeposit(_jia, finalBing, _time, _yajin, _tokenName, _notHalf, _notQuarter).send({
            from: accounts[0],
            to: contract._address,
            value: _yajin * 10000000000000000 //押金以0.01ether为单位
            // gas: 21000,
            // gasPrice: 20000000000,
          });
          await contract.events.CreateDeposit({
            fromBlock: 8478572
          }, function (error, event) {
            console.log(event);
            console.log(error);

            document.querySelector(".createDeposit").innerHTML = "Created successfully! Your deposit ID is" + event.returnValues[1];
          });
        } catch (error) {
          console.log(error);
          if (error.message === "MetaMask Tx Signature: User denied transaction signature.") {
            document.querySelector(".createDeposit").innerHTML = "User cancelled!";
          } else if (error.code === -32602) {
            document.querySelector(".createDeposit").innerHTML = "Please refresh the page and try again!";
            window.alert("Please refresh the page and try again!");
          } else {
            document.querySelector(".createDeposit").innerHTML = "Please enter the correct wallet address and check your network!";
          };

        };
      } else if (wETHcolor === "gold") {
        console.log(wETHcolor);
        let _tokenName = "Wrapped Ether";
        try {

          await rinkebywETH.methods.allowance(accounts[0], contract._address).call({
            from: accounts[0],
            to: rinkebywETH._address
          }).then(async function (result) {
            console.log(result);
            if (result < _yajin * 1000000000000000000) {
              await rinkebywETH.methods.approve(contract._address, "100000000000000000000").send({
                from: accounts[0],
                to: rinkebywETH._address
              })
            }


            await contract.methods.startDeposit(_jia, finalBing, _time, _yajin, _tokenName, _notHalf, _notQuarter).send({
              from: accounts[0],
              to: contract._address
              // value: _yajin * 10000000000000000
              // gas: 21000,
              // gasPrice: 20000000000,
            });
            await contract.events.CreateDeposit({
              fromBlock: 8478572
            }, function (error, event) {
              console.log(event);
              console.log(error);

              document.querySelector(".createDeposit").innerHTML = "Created successfully! Your deposit ID is" + event.returnValues[1];
            });


          })


        } catch (error) {
          console.log(error);
          if (error.message === "MetaMask Tx Signature: User denied transaction signature.") {
            document.querySelector(".createDeposit").innerHTML = "User cancelled!";
          } else if (error.code === -32602) {
            document.querySelector(".createDeposit").innerHTML = "Please refresh the page and try again!";
            window.alert("Please refresh the page and try again!");
          } else {
            document.querySelector(".createDeposit").innerHTML = "Please enter the correct wallet address and check your network!";
          };

        };
      }

    }
    else if (this.state.networkId === 212984383488152) {
      try {
        await contract.methods.startDeposit(_jia, finalBing, _time, _notHalf, _notQuarter).send({
          from: accounts[0],
          to: "0x1Fb89f92807381252ba247CCF1aA97E1204B20Bb",
          value: _yajin * 10000000000000000 //押金以0.01ether为单位
          // gas: 21000,
          // gasPrice: 20000000000,
        });
        await contract.events.CreateDeposit({
          fromBlock: 79170
        }, function (error, event) {
          console.log(event);
          console.log(error);

          document.querySelector(".createDeposit").innerHTML = "Created successfully! Your deposit ID is" + event.returnValues[1];
        });
      } catch (error) {
        console.log(error);
        if (error.message === "MetaMask Tx Signature: User denied transaction signature.") {
          document.querySelector(".createDeposit").innerHTML = "User cancelled!";
        } else if (error.code === -32602) {
          document.querySelector(".createDeposit").innerHTML = "Please refresh the page and try again!";
          window.alert("Please refresh the page and try again!");
        } else {
          document.querySelector(".createDeposit").innerHTML = "Please enter the correct wallet address and check your network!";
        };

      };
    }
    else {
      document.querySelector(".createDeposit").innerHTML = "Please check your network";
    }


    // Update state with the result.
    // this.setState({ storageValue: response });
  };






